const colors = [
    '#FFFFFF',
    '#2196F3',
    '#4CAF50',
    '#FF9800',
    '#009688',
    '#795548',
];

const refs = {
    start: document.querySelector('[data-action="start"]'),
    stop: document.querySelector('[data-action="stop"]'),
    body: document.querySelector('body')
};

const min = 0;
const max = colors.length - 1;

const randomIntegerFromInterval = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1) + min);
};

let changeColorsInterval = null; // внешняя переменная интервала для двух ф-ций Старт и Стоп

refs.start.addEventListener('click', onStartBtnClick);
refs.stop.addEventListener('click', onStopBtnClick);


function onStartBtnClick() {
    console.log('Start click');

    changeColorsInterval = setInterval(changeBgColors, 1000);
    refs.start.disabled = true;
};

function onStopBtnClick() {
    console.log('Stop click');

    clearInterval(changeColorsInterval);
    refs.start.disabled = false;
};


function changeBgColors() {
    refs.body.style.backgroundColor = colors[randomIntegerFromInterval(min, max)];
};


